import Vue from 'vue'
import Router from 'vue-router'
import financeView from '@/components/financeCalendar/financeView'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'financeView',
      component: financeView
    },
    {
      path: '*',
      redirect: 'financeView'
    }
  ]
})



